/* http://meyerweb.com/eric/tools/css/reset/
   v5.0.1 | 20191019
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, menu, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

menu, ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

* {
  box-sizing: border-box;
}

.srOnly {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
}

:root {
  --c-primary: hsl(300, 43%, 22%);
  --c-primary-light: hsl(333, 80%, 67%);
  --c-secondary: hsl(303, 10%, 53%);
  --c-secondary-light: hsl(300, 24%, 96%);
  --c-light: hsl(0, 0%, 100%);
}

.wrapper {
  background: top/100% auto no-repeat url(bg-pattern-top-mobile.svg), bottom/100% auto no-repeat url(bg-pattern-bottom-mobile.svg);
}
@media (min-width: 48rem) {
  .wrapper {
    background: top left no-repeat url(bg-pattern-top-desktop.svg), bottom right no-repeat url(bg-pattern-bottom-desktop.svg);
  }
}

.social-proof {
  max-width: 44rem;
  min-height: 100vh;
  margin: 0 auto;
  padding: 8.2rem 2.4rem 10rem;
}
@media (min-width: 48rem) {
  .social-proof {
    display: grid;
    grid-gap: 7rem 1.6rem;
    grid-template: "intro ratings" auto "testimonials testimonials" auto/1fr 1fr;
    align-content: start;
    align-items: center;
    max-width: none;
    padding-right: 4rem;
    padding-left: 4rem;
  }
}
@media (min-width: 64rem) {
  .social-proof {
    grid-gap: 7.2rem 3rem;
    padding-top: 13.1rem;
    padding-right: 6rem;
    padding-left: 6rem;
  }
}
@media (min-width: 75rem) {
  .social-proof {
    padding-right: 16.4rem;
    padding-left: 16.4rem;
  }
}
.social-proof__intro {
  display: grid;
  grid-area: intro;
  grid-row-gap: 2.4rem;
  max-width: 46rem;
  color: var(--c-primary);
}
@media (max-width: 47.99875rem) {
  .social-proof__intro {
    text-align: center;
  }
}
@media (min-width: 64rem) {
  .social-proof__intro {
    grid-row-gap: 1.3rem;
  }
}
.social-proof__ratings {
  display: grid;
  grid-area: ratings;
  grid-row-gap: 1.6rem;
  color: var(--c-primary);
}
@media (max-width: 47.99875rem) {
  .social-proof__ratings {
    margin-top: 3.8rem;
  }
}
.social-proof__testimonials {
  grid-area: testimonials;
}
@media (max-width: 47.99875rem) {
  .social-proof__testimonials {
    margin-top: 5rem;
  }
}

.grid {
  --margin: 0.8rem;
  display: flex;
  flex-flow: column;
  margin: calc(var(--margin) * -1);
}
@media (min-width: 48rem) {
  .grid {
    flex-flow: row wrap;
    justify-content: center;
  }
}
@media (min-width: 75rem) {
  .grid {
    margin: -1.5rem;
  }
}
.grid__item {
  margin: var(--margin);
}
@media (min-width: 48rem) {
  .grid__item {
    flex: 0 0 calc(50% - var(--margin) * 2);
  }
}
@media (min-width: 64rem) {
  .grid__item {
    flex-basis: calc(33.33% - var(--margin) * 2);
  }
  .grid__item:nth-child(1) {
    margin-bottom: calc(1.6rem * 2 + var(--margin));
  }
  .grid__item:nth-child(2) {
    margin-top: calc(1.6rem + var(--margin));
    margin-bottom: calc(1.6rem + var(--margin));
  }
  .grid__item:nth-child(3) {
    margin-top: calc(1.6rem * 2 + var(--margin));
  }
}
@media (min-width: 75rem) {
  .grid__item {
    --margin: 1.5rem;
  }
}

.card {
  display: grid;
  grid-gap: 2.6rem;
  align-content: start;
  padding: 4rem 3.2rem 3.2rem;
  color: var(--c-light);
  background-color: var(--c-primary);
  border-radius: 0.8rem;
}
@media (min-width: 64rem) {
  .card {
    padding-bottom: 4rem;
  }
}

.rating {
  display: grid;
  grid-gap: 1.6rem 3.2rem;
  justify-items: center;
  padding: 1.6rem;
  background-color: var(--c-secondary-light);
  border-radius: 0.8rem;
}
@media (min-width: 64rem) {
  .rating:nth-child(1) {
    margin-right: 9.6rem;
  }
  .rating:nth-child(2) {
    margin-right: 4.8rem;
    margin-left: 4.8rem;
  }
  .rating:nth-child(3) {
    margin-left: 9.6rem;
  }
}
@media (min-width: 75rem) {
  .rating {
    grid-auto-flow: column;
    align-items: center;
    justify-content: start;
    padding: 2rem 3.2rem;
  }
}

.stars {
  width: 11.7rem;
  height: 1.6rem;
  background: left center url(icon-star.svg);
}

.person {
  display: grid;
  grid-gap: 0 2.4rem;
  grid-template: "avatar name" auto "avatar info" auto/auto 1fr;
  align-items: center;
}
.person__avatar {
  grid-area: avatar;
}
.person__name {
  grid-area: name;
}
.person__info {
  grid-area: info;
  color: var(--c-primary-light);
}

.avatar {
  max-width: 4rem;
  border-radius: 50%;
}

.quote {
  font: 1.4rem/160% Spartan, sans-serif;
  letter-spacing: -0.05em;
}
.quote::before {
  content: '" ';
}
.quote::after {
  content: ' "';
}

.h2 {
  font-weight: 700;
  font-size: calc( 3.2rem + 1.6 * ((100vw - 37.5rem) / 106.5) );
  font-family: Spartan, sans-serif;
  line-height: 100%;
  letter-spacing: -0.04em;
}

.text {
  font: 500 1.5rem/170% Spartan, sans-serif;
  letter-spacing: -0.03em;
}

.text-subtitle {
  font: 700 1.4rem/100% Spartan, sans-serif;
  letter-spacing: -0.04em;
}

.text-secondary {
  font: 1.3rem Spartan, sans-serif;
}